import React from 'react';

// Constants, types
import { BarDatum, BarSvgProps, ResponsiveBar } from '@nivo/bar';
import { NivoBar } from '../types';

type BarChartNivoProps = Omit<BarSvgProps<BarDatum>, 'data' | 'width'>;

export type BarChartProps = {
  /** The chart data to be passed to the chart */
  chartData: NivoBar[];
  /** All additional Nivo props that can be passed */
  nivoProps?: BarChartNivoProps;
};

const defaultProps = {
  enableGridX: false,
  enableGridY: false,
  colorBy: 'indexValue' as const,
  margin: { top: 25, right: 100, bottom: 25, left: 100 },
};

const BarChart: React.FC<BarChartProps> = ({ chartData, nivoProps }) => {
  return <ResponsiveBar {...defaultProps} {...nivoProps} data={chartData} />;
};

export default BarChart;
